<template>
  <div class="about">
    <h1>Esta es una ruta protegida</h1>
    {{token}}
  </div>
</template>

<script>
import {mapState} from "vuex";
export default {
  computed: {
    ...mapState(["token"])
  },
  methods:{
    async datosProtegidos(){
      try{
        const res = await fetch("https://my-first-api63.herokuapp.com/api/admin",{
          headers:{
            "Content-Type": "application/json",
            "auth-token": this.token
          }
        });

        const resDB = await res.json();
        console.log(resDB);
      }catch(error){

      }
    }
  },
  created(){
    this.datosProtegidos()
  }
}
</script>

